.modal {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content {
    width: 60%;
    background-color: #ffffff;
}

.modal-header, .modal-footer {
    padding: 10px;
}

.modal-title {
    margin: 0;
}

.modal-body {
    padding: 10px;
    border-top: 1px solid #eeeeee;
    border-bottom: 1px solid #eeeeee;
}
:root {
    --main-background: #765285;
    --main-background-dark: #351c4d;
    --main-pop-colour: #ff7e5f;
    --main-pop-colour-dark: #f26968;
    --main-pop-colour-light: #f2ad9f;
    --secondary-pop-colour: #3F8988;
    --secondary-pop-colour-dark: #2b6777;
    --secondary-pop-colour-light: #52ab98;

}

body, html{
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 500;

}
input {
    margin: 10px;
}

button {
    border: none;
    align-items: flex-start;
    outline: none;
    background-color: #765285;
    background-color: var(--main-background);
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin: 10px;
}
button:hover {
    background-color: #351c4d;
    background-color: var(--main-background-dark);
}

input {
    padding: 10px;
    border-radius: 5px;
}

select {
    width: 80%;
    padding: 10px;
    border-radius: 5px;
    margin: 10px;
}


#headerBar {
    padding: 20px;

    background-color: #3F8988;

    background-color: var(--secondary-pop-colour);
    color: #ff7e5f;
    color: var(--main-pop-colour);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#main {
    background-color: #765285;
    background-color: var(--main-background);
    display: flex;

    justify-content: center;
}

#optionsPane {
    background-color: #351c4d;
    background-color: var(--main-background-dark);
    width: 300px;
    min-width: 250px;
    padding: 20px;
}

#taskPane {
    background-color: #765285;
    background-color: var(--main-background);
    width: 900px;
    padding: 20px 40px;
    margin: 0 auto;
}

#footerBar {
    background-color: #ff7e5f;
    background-color: var(--main-pop-colour);
}

#root {
    background-color: purple;
}


#newTaskInterface {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 100%;
    text-align: center;
}

.overview-container {
    display: flex;
    flex-direction: column;
}

.task-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    margin: 5px;
    border-radius: 5px;

    background-color: #ff7e5f;

    background-color: var(--main-pop-colour);
}
